@import url("https://fonts.googleapis.com/css2?family=Syne:wght@400..800&display=swap");

/* hero */

@media (max-width: 1024px) {
  .hero {
    flex-wrap: wrap;
    height: max-content;
  }
  .bulb {
    position: relative;
    right: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    img {
      width: 300px;
    }
  }
  .hero-container {
    display: block;
    padding-bottom: 2em;
    width: 100%;
  }
  .hero-left,
  .hero-right {
    width: 100%;
    text-align: center;
  }
  .buy-btn {
    display: flex;
    justify-content: center;
  }
  .hero-content {
    margin-top: 2em;
    justify-content: space-evenly;
  }
  .hero-light-btn {
    flex-direction: row;
    justify-content: center;
  }
  .hero-con-wrap {
    flex-direction: column-reverse;
  }
  .second-sec {
    flex-direction: column;
    text-align: center;
  }
  .third-con {
    flex-wrap: wrap;
  }
  .card-small {
    text-align: center;
  }
}

@media (max-width: 676px) {
  .third-con a {
    font-size: 0.75rem;
  }
  .third-con p.text-lg {
    font-size: 1rem;
  }
}
